
import { defineComponent, computed, ref, watch } from 'vue'
import { useStore } from 'vuex'
import hyTable from '@/base-ui/table'
import { usePermission } from '@/hooks/use-permission'

export default defineComponent({
  components: {
    hyTable
  },
  props: {
    pageName: {
      type: String,
      required: true
    },
    contentTableConfig: {
      type: Object,
      required: true
    }
  },
  emits: ['handleNewClick', 'handleEditClick'],
  setup(props, { emit }) {
    const store = useStore()

    //获取操作权限
    const isCreate = usePermission(props.pageName, 'create')
    const isUpdate = usePermission(props.pageName, 'update')
    const isDelete = usePermission(props.pageName, 'delete')
    const isQuery = usePermission(props.pageName, 'query')
    //网络请求
    const getPageData = (queryInfo: any = {}) => {
      if (!isQuery) return
      store.dispatch('getPageListAction', {
        pageName: props.pageName,
        queryInfo: {
          offset: pageInfo.value.pageSize * (pageInfo.value.currentPage - 1),
          size: pageInfo.value.pageSize,
          ...queryInfo
        }
      })
    }

    //修改分页器
    const pageInfo = ref({ pageSize: 10, currentPage: 1 })
    watch(pageInfo, () => getPageData())
    getPageData()

    //3.从vuex中获取数据
    const dataCount = computed(() =>
      store.getters['pageListCount'](props.pageName)
    )
    const dataList = computed(() =>
      store.getters['pageListData'](props.pageName)
    )
    //4.获取其他动态插槽名称
    const otherPropSlot = props.contentTableConfig.propList.filter(
      (item: any) => {
        if (item.slotName === 'status') return false
        if (item.slotName === 'createAt') return false
        if (item.slotName === 'updateAt') return false
        if (item.slotName === 'handler') return false
        return true
      }
    )

    //删除/编辑/新建按钮
    const handleDeleteClick = (item: any) => {
      store.dispatch('deletePageDataAction', {
        pageName: props.pageName,
        id: item.id
      })
    }
    const handleNewClick = () => {
      console.log('111')

      emit('handleNewClick')
    }
    const handleEditClick = (item: any) => {
      emit('handleEditClick', item)
    }

    // expose({ getPageData })
    return {
      dataList,
      getPageData,
      dataCount,
      pageInfo,
      otherPropSlot,
      isCreate,
      isUpdate,
      isDelete,
      isQuery,
      handleDeleteClick,
      handleNewClick,
      handleEditClick
    }
  }
})
